import React, { Fragment, useState, useEffect } from 'react'
import Axios from 'axios'

const TextBox = () => {
    const [thought, setThought] = useState({})

    const getText = () => {
        Axios.get('https://justmt.000webhostapp.com/getText')
            .then(response =>{
                if(response.data['Status']==="400"){
                    setThought({texto:"none"})
                }else{
                    setThought(response.data[0])
                }

            })
            .catch((error) => {
                console.log(error)
            })
    }

    // const getText = async () =>{
    //     const resultado = await Axios.get('http://localhost:8000/getText')
    //     setThought(resultado.data[0])
    //     console.log(thought)

    useEffect(() => {
        getText()
        console.log(thought)
    }, [])
    return (
        <div>
            <div className="h-align-center mt-1">
                <p className="i-tittle h-align-center">People's thoughts</p>
            </div>
            <div>
                <div className="box-i my-auto bg-secondary">
                    {thought.texto}
                </div>

            </div>
            <div className="center-btn">
                <button className="btn-primary i-tittle my-auto" onClick={getText}>See more!</button>
            </div>
        </div>
    );
}

export default TextBox;