import React, { Fragment } from 'react';
import { useForm } from "react-hook-form";
import '../CSS/boxes.css'
import Axios from 'axios';

const SubmitText = () => {
    const { register, handleSubmit, watch, errors , reset} = useForm();
    const headers = {
        'Content-Type': 'multipart/form-data',
    }
    const onSubmit = data => {
        console.log(data)
        Axios.post('https://justmt.000webhostapp.com/genNew',
        {texto:data.texto},
        {headers:headers}
        )
        reset()
    }
    return (
        <div className="mb-0">
            <div className="h-align-center mt-1">
                <p className="i-tittle">Share your thoughts</p>
            </div>
            <form className="box-i my-auto mt-s0" onSubmit={handleSubmit(onSubmit)}>
                <textarea className="box-i bg-secondary" maxLength={500} name="texto" 
                ref={register({
                    required:true
                })}>
                </textarea>
                <button className="btn-primary i-tittle">Send</button>
            </form>
        </div>
    );
}

export default SubmitText;